.App {
  text-align: center;
  background-image: url('photo-1533134486753-c833f0ed4866.jpg');
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

/*=================================================*/

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  background-color: transparent;
  position: -webkit-sticky;
  position: sticky;
  top: 0;

}

li {
  float: left;
  font-size: 1.2rem;
}

li a {
  display: block;
  color: white;
  text-align: center;
  padding: 24px 120px;
  text-decoration: none;
  transition: 0.8s;
  transition: all 0.8s ease-in-out;
  font-weight: 600;
  font-size: 1.2rem;
}

li a:hover:not(.switch-mode){
  background-color: pink;
  color: black;
  letter-spacing: 5px;
  font-weight: 600;
}

.switch-mode{
  color: #fff;
  text-shadow:
    0 0 2px #fff,
    0 0 5px #fff,
    0 0 6px #fff,
    0 0 9px #0fa,
    0 0 82px #0fa,
    0 0 92px #0fa,
    0 0 102px #0fa,
    0 0 151px #0fa;
}

.container {
  color: white;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  height: auto;
  gap: 4vh;
  padding: 4vh 10vh;
}

.column {
  flex-direction: column;
  width: 400px;
  height: 640px;
  border-radius: 16px;
  overflow: hidden;
  transition: all 1s;
}

.card>img {
  width: 100%;
  height: 400px;
  object-fit: cover;
  padding-bottom: 1.8vh;
  border-radius: 10%;
  transition: all 0.8s ease-in-out;
}

.card {
  transition: all 0.8s ease-in-out;
}

.nav-cont {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.column:hover:not(.button, .popup) {
  transform: scale(1.1);
  border: unset;
}

button {
  width: 100px;
  color: white;
  font-size: 1.2rem;
  font-weight: 700;
  border: unset;
  transition: all 1s;
  background: unset;
}

button:hover {
  width: 100%;
  background: rgba(250, 248, 213, 0.6);
  white-space: 20px;
  letter-spacing: 5px;
  color: black;
}

/*=============== Switch Theme Toggle ===================*/
.switch {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 34px;
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: .4s;
  transition: .4s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  -webkit-transition: .4s;
  transition: .4s;
}

input:checked+.slider {
  background-color: #2196F3;
}

input:focus+.slider {
  box-shadow: 0 0 1px #2196F3;
}

input:checked+.slider:before {
  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  transform: translateX(26px);
}

/* Rounded sliders */
.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}

/*===============Pop-up Detail===================*/

.overlay {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(255, 255, 255, 0.2);
  transition: opacity 500ms;
  visibility: hidden;
  opacity: 0;
  backdrop-filter: blur(3px);
  color: wheat;
  font-size: large;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 8vh;
}

.overlay:target {
  visibility: visible;
  opacity: 1;
}

.popup {
  margin: 70px auto;
  width: 700px;
  height: auto;
  position: relative;
  transition: all 1s ease-in-out;
  background-color: black;
  padding-bottom: 5vh;
  text-align: left;
  border-bottom: 1px solid wheat;
  border-right: 2px solid wheat;
  border-left: 2px solid wheat;
}

.popup img {
  width: 100%;
  border-radius: unset;
}

.popup h2 {
  margin-top: 0;
  color: #333;
  font-size: 1.3em;
}

.popup .content {
  padding-left: 5vh;
  padding-right: 5vh;
  transform: unset;
}

.popup .close {
  position: absolute;
  top: 6px;
  right: 20px;
  transition: all 200ms;
  font-size: 50px;
  font-weight: bold;
  text-decoration: none;
  color: #333;
}

.popup .close:hover {
  color: #06D85F;
}

.popup .content {
  max-height: 30%;
  overflow: auto;
}

